.html {
  width: 100%;
  height: 100%;
  min-height: 100%;
}
body {
  margin: 0;
  background: #0b0c10;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  width: 100vw;
}
#root {
  display: block;
  height: 100%;
  width: 100%;

  background: #0b0c10;
}
.App {
  background: #0b0c10;
  color: #66fcf1;
  height: 100%;
  width: 100%;
  display: block;
}
.table tr {
  color: #c5c6c7;
}
.tbale tr:hover {
  color: #950740;
}
.action-btn {
  display: flex;
}
.add-contact {
  background: #c3073f;
  color: #c3073f;
}
.save-contact {
  background: #c3073f;
}
.input-search {
  width: 50%;
  height: 100%;
}
